<template>
  <button
    class="app-button claim-button"
    @click="user_id && isWrongNetwork ? wrongNetClick() : $emit('claim')"
  >
    {{
      user_id
        ? isWrongNetwork
          ? "Select ethereum network"
          : "Initiate Claim"
        : "Connect wallet"
    }}
  </button>
</template>

<script>
export default {
  name: "claim-button",
  props: {
    user_id: String,
    isWrongNetwork: Boolean,
  },
  methods: {
    wrongNetClick() {
      window.netWorkButtonClick();
    },
  },
};
</script>

