<template>
  <transition name="fade">
    <div id="app">
      <transition name="fade">
        <app-loader v-if="loading" :loading="loading" />
      </transition>

      <Header :user_id="user_id" @connect="connectWallet"></Header>
      <div class="home-wrap">
        <Claim
          :user_id="user_id"
          :default_links="default_links"
          :isWrongNetwork="isWrongNetwork"
        >
          <template v-slot>
            <claim-button
              :user_id="user_id"
              :isWrongNetwork="isWrongNetwork"
              @claim="claimAction"
            />
          </template>
        </Claim>
      </div>

      <Claim-Info :user_id="user_id">
        <template v-slot>
          <claim-button
            :user_id="user_id"
            :isWrongNetwork="isWrongNetwork"
            @claim="claimAction"
          />
        </template>
      </Claim-Info>
      <Distribution />

      <Footer :default_links="default_links"></Footer>
    </div>
  </transition>
</template>

<script>
import Header from "@/components/Header";
import Footer from "@/components/Footer";

import Distribution from "@/components/Distribution";
import Claim from "@/components/Claim";
import ClaimInfo from "@/components/ClaimInfo";
import ClaimButton from "@/components/ClaimButton";
import AppLoader from "@/components/Loader.vue";

import { bus } from "./main";

export default {
  name: "app",
  components: {
    AppLoader,
    Footer,
    Header,
    Distribution,
    Claim,
    ClaimInfo,
    ClaimButton,
  },
  created() {
    //bus.$on("setNotifyLink", this.setNotifyLink);
    bus.$on("setUserAccountId", this.setUserAccountId);
    bus.$on("selectAccessMint", this.selectAccessMint);
    bus.$on("updateLoaderStatus", this.updateLoaderStatus);

    this.DOMAIN_NAME = window.location.host;
  },
  mounted() {
    if (localStorage.getItem("autoConnect")) this.autoConnect = true;
    if (this.autoConnect && !this.isMobile()) this.connectWallet();
  },
  beforeDestroy() {
    bus.$off("setUserAccountId", this.setUserAccountId);
    bus.$off("selectAccessMint", this.selectAccessMint);
    bus.$on("updateLoaderStatus", this.updateLoaderStatus);
    // bus.$off("setNotifyLink", this.setNotifyLink);
  },
  data() {
    return {
      loading: false,
      DOMAIN_NAME: "",
      user_ETH: 0,
      user_id: null,
      autoConnect: false,
      isWrongNetwork: false,
      // mint: null,

      default_links: [
        /* {
          id: "dis",
          to: "https://discord.com/invite/memeland",
        },*/
        {
          id: "inst",
          to: "https://instagram.com/rarity_tools",
        },
        {
          to: "https://twitter.com/rarity_tools",
          id: "tw",
        },
      ],
    };
  },
  methods: {
    updateLoaderStatus(status = false) {
      this.loading = status;
    },
    selectAccessMint(val) {
      this.isWrongNetwork = !val;
    },
    isMobile() {
      const toMatch = [
        "Android",
        "iPhone",
        "iPad",
        "iPod",
        "BlackBerry",
        "Windows Phone",
      ];

      return toMatch.some((toMatchItem) => {
        return navigator.userAgent.match(toMatchItem);
      });
    },
    setUserAccountId(id) {
      if (!id) return (this.user_id = null);
      this.user_id = id;
      localStorage.setItem("autoConnect", true);
    },
    connectWalletDesktop() {
      window.connectButtonClick();
    },
    async connectWalletMobile() {
      window.location.href = `https://metamask.app.link/dapp/${this.DOMAIN_NAME}/`;
    },
    async connectWallet() {
      if (window.ethereum) {
        return this.connectWalletDesktop();
      } else {
        if (this.isMobile()) return this.connectWalletMobile();
        return alert("Please install metamask!");
      }
    },
    async claimAction() {
      //    this.mint = true;

      if (!this.user_id) return this.connectWallet();
      this.loading = 1;
      window.mintButtonClick();

      //  this.mint = null;
    },
    setNotifyLink(hash) {
      this.$notify({
        duration: 5000,
        group: "custom-template",
        title: "Transaction submitted:",
        text: `https://etherscan.io/tx/${hash}`,
      });
    },
  },
};
</script>

